import match from "./match";
export default function getColorBoule(val) {
    const result = match(val)
        .on((n) => ["2", "4", "7", "9"].includes(n), () => "red")
        .on((n) => ["1", "3", "6", "8"].includes(n), () => "blue")
        .on((n) => n === "5", () => "green")
        .otherwise((n) => () => "normal");
    return result;
}
